.agrace{
    align-items: center;
    justify-content: center;
    cursor: pointer;
    height: 100vh;
} 
p{
    color:rgb(255,255,255);
    font-size: 20px;
}
.line{
    width:55%;
    align-items: center;
    justify-content: center;
    border-right: 1px solid white;
    animation: blickCursor 500ms infinite normal, typing 5s 1s normal;
    white-space: nowrap;
    overflow: hidden;
    margin: 2rem auto;
}
@keyframes typing{
    from{
        width:0;
    }
    to{
        width:48rem;
    }

}
@keyframes blickCursor{
    from{
        border-right-color: white;
    }
    to{
        border-right-color: transparent ;
    }

}
