.foto{
    cursor: pointer;
    column-count: 3;
}
.foto img{
    margin:0px;
    width:30%;
}
.foto > .fotodela img {
    position: absolute;
    top:10%;
    left: 3%;
    left: calc(50% - auto);
    opacity: 0;
    animation: display 9s infinite;
    height:auto;
}
.foto > .fotodeles img {
    position: absolute;
    top:10%;
    left: 35%;
    left: calc(50% - auto);
    opacity: 0;
    animation: display 9s infinite;
    height:auto;
}
.foto > .fotodeles1 img {
    position: absolute;
    top:55%;
    left: 35%;
    left: calc(50% - auto);
    opacity: 0;
    animation: display 9s infinite;
    height:auto;
}
.foto > .fotodele img {
    position: absolute;
    top:10%;
    left: 69%;
    left: calc(50% - auto);
    opacity: 0;
    animation: display 9s infinite;
    height:auto;
}


@keyframes display {
    0% {
        transform: translateX(50px);
        opacity: 0;
    }
    10% {
        transform: translateX(30px);
        opacity: 0;
    }
    25% {
      transform: translateX(10px);
      opacity: 1;
    }
    25% {
      transform: translateX(0);
      opacity: 1;
    }
    25% {
        transform: translateX(0);
        opacity: 1;
      }
    35% {
      transform: translateX(-10px);
      opacity: 0;
    }
    80% {
        transform: translateX(-60px);
        opacity:0;
    }
  }
@media(max-width:1200px){
    .foto img{
        width:30%;
    }
    .foto > .fotodeles1 img {
        top:50%;
    }
}   
@media(max-width:900px){
    .foto img{
        width:36%;
    }
    .foto > .fotodela img {
        top:5%;
        left: 3%;
    }
    .foto > .fotodeles1 img {
        top:65%;
        left: 60%;
    }
    .foto > .fotodeles img {
        top:65%;
        left: 3%;
    }
    .foto > .fotodele img {
        top:5%;
        left: 60%;
    }
}   
@media(max-width:700px){
    .foto img{
        width:45%;
    }   
    .foto > .fotodela img {
        top:10%;
        left: 3%;
    }
    .foto > .fotodeles img {
        top:65%;
        left: 51%;
    }
    .foto > .fotodeles1 img {
        top:65%;
        left: 3%;
    }
    .foto > .fotodele img {
        top:10%;
        left: 51%;
    }

} 

.fotodela img:nth-child(1) {
    animation-delay:1s;
}
.fotodela img:nth-child(2) {
    animation-delay:15s;
}
.fotodela img:nth-child(3) {
    animation-delay:35s;
}


.fotodeles img:nth-child(1) {
    animation-delay:1s;
}
.fotodeles img:nth-child(2) {
    animation-delay:15s;
}
.fotodeles img:nth-child(3) {
    animation-delay:35s;
}

.fotodeles1 img:nth-child(1) {
    animation-delay:1s;
}
.fotodeles1 img:nth-child(2) {
    animation-delay:15s;
}
.fotodeles1 img:nth-child(3) {
    animation-delay:35s;
}



.fotodele img:nth-child(1) {
    animation-delay:1s;
}

.fotodele img:nth-child(2) {
    animation-delay:15s;
}
.fotodele img:nth-child(3) {
    animation-delay:35s;
}